@import 'variables';

*, ::after, ::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    padding: 0;
    margin: 0;
    background: $white-color;
    font-family: Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

p {
    margin-bottom: 0;
}

img {
    border: none;
}

header, footer {
    margin: auto;
    max-width: 1920px;
}

main {
    margin: auto;
}

a {
    text-decoration: none;
    cursor: pointer;
}

button {
    border: none;
    cursor: pointer;
}

ul {
    list-style: none;
}


.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.order {
    &__header {
        margin-bottom: 50px;
        font-family: inherit;
        font-weight: 600;
        font-size: 28px;
        line-height: 1;
        color: #000;
    }

    &__layout {
       display: flex; 
       gap: 30px;
    }

    &__left {
        width: 770px;
    }

    &__right {
        width: 370px;
    }

    &__tab {
        width: 270px;
        height: 54px;
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        text-transform: uppercase;
        text-align: center;
        color: #000;
        background: none;
        border: 1px solid #EEEEEE;
        // border-bottom: none;
    }

    &__panel {
        padding: 30px;
    }
}

.tabs {

    &__nav {
        margin-bottom: -1px;
        display: flex;
        gap: 18px;

    }

    &__nav-btn {
        &_active {
            border-bottom: 1px solid #ffffff;
        }
    }

    &__panel {
        display: none;
        width: 100%;
        height: 500px;
        border: 1px solid #EEEEEE;

        // &:nth-child(1) {
        //     background: green;
        // }
        // &:nth-child(2) {
        //     background: blue;
        // }

        &_active {
            display: block;
            
        }

    }
   


}





//media-queries
@media (min-width: 768px) {
    .container {
        width: 750px
    }

   

}

@media (min-width: 992px) {
    .container {
        width: 970px
    }
}

@media (min-width: 1230px) {
    .container {
        width: 1200px
    }

   
}